import React, { useEffect, useState } from "react";
import "./App.css";

function App() {
  const [japidorCoins, setJapidorCoins] = useState(0);
  const [balance, setBalance] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setJapidorCoins((prevCoins) => prevCoins + 1); // Майнинг по 1 коину
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  const collectCoins = () => {
    setBalance((prevBalance) => prevBalance + japidorCoins);
    setJapidorCoins(0); // Обнуляем накопленные коины после забора
  };

  return (
    <div className="app">
      <div className="balance">Баланс: {balance} JC</div>
      <h1>JapidorCoin Miner</h1>
      <div className="gif-container">
        <img src="/assets/miner.gif" alt="Mining" className="miner-gif" />
      </div>
      <div className="coins">Накопленные коины: {japidorCoins}</div>
      <button className="collect-button" onClick={collectCoins}>
        Забрать коины
      </button>
    </div>
  );
}

export default App;
